//Libraries
import React, { Component } from 'react';
import {Redirect} from 'react-router-dom';
import { Form, Select, Button, Col, Row, Collapse, InputNumber, Input, Tooltip, Icon, Table} from 'antd';
import connect from 'react-redux/es/connect/connect';

//Subcomponents
import FieldTitle from '../../subcomponents/FieldTitle';
import routes from '../../../../configuration/routing/Routes';

//Styles
import '../../../styles/admin/create-company.css';
import { SUCCESS_MODAL, ERROR_MODAL, WARNING_MODAL } from '../../subcomponents/modalMessages';

//Actions
import {createCompany}  from "../../../../store/redux/actions/admin/adminActions";

//Constants
//import {Roles} from "../../../lib/generalUtils/constants";
const FormItem = Form.Item;
const { Panel } = Collapse;

const columns = [
  {
    title: 'Tipo de ciclo',
    dataIndex: 'companyRate',
  },
  {
    title: 'Fecha de reporte',
    dataIndex: 'companyReportDate',
  },
  {
    title: 'Fecha de pago 1',
    dataIndex: 'companyFirstDate',
  },
  {
    title: 'Fecha de pago 2',
    dataIndex: 'companySecondDate',
  },
];

class CreateCompany extends Component {

  constructor (props) {

    super(props);
    
    this.state = {
      isLoading: false,
      captchaSolved: true,
      email: null,
      meeting: null,
      moyen: false,
      report: null,
      loan: null,
      upload: null,
      salary_rate: null,
      salariesArray: [0],
      companyRateName: null,
      companyPaymentNumber: null,
      companyRate: null,
      companyReportDate: null,
      companyFirstDate: null,
      companySecondDate: null,
      companySalaries: [],
      burstingKey: 1,
    };

  };

  onConfirmRequest = () => {
    SUCCESS_MODAL("Accin realizada exitosamente", "La empresa ha sido creada correctamente.");
  };

  handleSalaryRate = () => {
    this.props.form.validateFields((err, values) => {
      //console.log(values.companyRate);
      if(values.companyRate === "Mensual"){
        this.setState({
          salary_rate: false,
        });
      }else{
        this.setState({
          salary_rate: true,
        });
      }
    });
  };

  createCompany = () => {
    this.props.form.validateFields((err, values) => {
      if (err){
        ERROR_MODAL("Error al realizar la accin", "Por favor ingrese datos vlidos dentro del formulario.");
      }else{
        let newSalary = [{
          companyRate: values.companyRate,
          companyReportDate: values.companyReportDate,
          companyFirstDate: values.companyFirstDate,
          companySecondDate: values.companySecondDate,
        }];
        let data = {
          address: values.address,
          approveHumanResources: values.approveHumanResources,
          companySalaries: this.state.companySalaries.length === 0 ? newSalary : this.state.companySalaries,
          defaultAmount: values.defaultAmount,
          economyActivity: values.economyActivity,
          email: values.email,
          maximumSplit: values.maximumSplit,
          nit: values.nit,
          socialReason: values.socialReason,
          password: values.password,
          companyMembers: [
            {
              memberName: values.memberName + "-" + values.memberLastName,
              memberId: values.memberId,
              profession: values.profession,
              phoneNumber: values.phoneNumber,
              memberEmail: values.memberEmail,
            },
            {
              memberName: values.representantName + "-" + values.representantLastName,
              memberId: values.representantId,
              profession: values.representantProfession,
              phoneNumber: values.representantPhone,
              memberEmail: values.representantEmail,
            }
          ],               
        };
        this.props.createCompany(data);
      }
    });
  };

  addNewSalary = () => {
    let {companyRate, companyReportDate, companyFirstDate, companySecondDate} = this.state;

    let datesArray = this.state.companySalaries;

    let newSalary = {
      companyRate: companyRate,
      companyReportDate: companyReportDate,
      companyFirstDate: companyFirstDate,
      companySecondDate: companySecondDate,
    };

    if(companyRate === null && companyReportDate=== null && companyFirstDate === null){
      WARNING_MODAL("Advertencia", "Los campos de ciclos de pagos no son vlidos.")
    }else{
      this.setState({
        companySalaries: datesArray,
        burstingKey: this.state.burstingKey+1,
        companyRate: null,
        companyReportDate: null,
        companyFirstDate: null,
        companySecondDate: null,
      });
      datesArray.push(newSalary);
    }
    

    
    
  };

  changeSalariesValues = (e, param) => {
    
    if(param === "companyRate"){
      //console.log("E-P", e, param);
      this.setState({
        [param]: e
      });
    }else{
      //console.log("E-P", e.target.value, param);
      this.setState({
        [param]: e.target.value
      });
    }
    
  };

  render(){
    
    let {getFieldDecorator} = this.props.form;
    let {companySalaries} = this.state;

    return (
      <div className={"company-div"}>
          <Row className={"request-row-content"}>
            <div>  
              <Form>
                <Collapse defaultActiveKey={['1']} bordered={false}>
                  <Panel header="Informacin general" key="1">
                    <Row gutter={20} className={"form-request-rows"}>
                      <Col xs={12} sm={12} md={8} lg={6} >
                        <FieldTitle title={"Razn social"}/>
                        <FormItem>
                          {getFieldDecorator('socialReason',
                            {rules: [
                              {required: true, message: 'Por favor ingresa una razn social'}
                            ]})(
                              <Input className={"form-input-number"} placeholder={"Razn social"} />
                            )
                          }
                        </FormItem>
                      </Col>
                      
                      <Col xs={12} sm={12} md={8} lg={6}>
                        <FieldTitle title={"NIT"}/>
                        <FormItem >
                          {getFieldDecorator('nit',
                            {rules: [
                              {required: true, message: 'Por favor ingresa un NIT vlido' }
                            ]})(
                              <InputNumber className={"form-input-number"} placeholder={"NIT"}/>
                            )
                          }
                        </FormItem>  
                      </Col>
                      <Col xs={12} sm={12} md={8} lg={6} >
                        <FieldTitle title={"Correo Electrnico"}/>
                        <FormItem>
                          {getFieldDecorator('email',
                            {rules: [
                              {required: true, message: 'Por favor ingresa un correo electrnico'}
                            ]})(
                              <Input className={"form-input-number"} placeholder={"Correo electrnico"} />
                            )
                          }
                        </FormItem>
                      </Col>
                      <Col xs={12} sm={12} md={8} lg={6} >
                        <FieldTitle title={"Contrasea por defecto"}/>
                        <FormItem>
                          {getFieldDecorator('password',
                            {rules: [
                              {required: true, message: 'Por favor ingresa una contrasea por defecto'}
                            ]})(
                              <Input className={"form-input-number"} placeholder={"Contrasea por defecto"} />
                            )
                          }
                        </FormItem>
                      </Col>
                      <Col xs={12} sm={12} md={5} lg={6}>
                        <FieldTitle title={"Actividad econmica"}/>
                        <FormItem >
                          {getFieldDecorator('economyActivity',
                            {rules: [
                              {required: true, message: 'Por favor ingresa una actividad econmica'}
                            ]})(
                              <Input className={"form-input-number"} placeholder={"Actividad econmica"}/>
                            )
                          }
                        </FormItem>  
                      </Col>
                      <Col xs={12} sm={12} md={5} lg={6}>
                        <FieldTitle title={"Direccin empresa"}/>
                        <FormItem >
                          {getFieldDecorator('address',
                            {rules: [
                              {required: true, message: 'Por favor ingresa una direccin'}
                            ]})(
                              <Input className={"form-input-number"} placeholder={"Direccin"} />
                            )
                          }
                        </FormItem>  
                      </Col>
                    </Row>
                  </Panel>
                  <Panel header="Informacin de crditos" key="2">
                    <Row gutter={8} className={"form-request-rows"}>
                      <Col xs={12} sm={12} md={5} lg={8}>
                          <FieldTitle title={"Aprueba Recursos Humanos?"}/>
                          <FormItem >
                            {getFieldDecorator('approveHumanResources',
                              {rules: [
                                {required: true, message: 'Por favor ingresa si se aprueba desde recursos humanos'}
                              ]})(
                                <Select placeholder={"Aprueba Recursos Humanos?"} showSearch={true} allowClear={true} autoClearSearchValue={true}>
                                  <Select.Option value={"S"}>S</Select.Option>
                                  <Select.Option value={"No"}>No</Select.Option>
                                </Select>
                              )
                          }
                        </FormItem>  
                      </Col>
                      <Col xs={12} sm={12} md={5} lg={8}>
                          <FieldTitle title={"Mxima cantidad a prestar"}/>
                          <FormItem >
                            {getFieldDecorator('defaultAmount',
                              {rules: [
                                {required: true, message: 'Por favor ingresa una monto particular'}
                              ]})(
                                <Input className={"form-input-number"} placeholder={"Mxima cantidad a prestar"}/>
                              )
                          }
                        </FormItem>  
                      </Col>
                      <Col xs={12} sm={12} md={5} lg={8}>
                        <FieldTitle title={"Cantidad de cuotas mxima"}/>
                        <FormItem >
                          {getFieldDecorator('maximumSplit',
                            {rules: [
                              {required: true, message: 'Por favor ingresa un mximo de cuotas'}
                            ]})(
                              <Input className={"form-input-number"} placeholder={"No. mximo de cuotas"} />
                            )
                          }
                        </FormItem>  
                      </Col>
                    </Row>
                    <Row className={"form-request-rows"}>
                      {
                        this.state.companySalaries.length>0 && 
                        <Table columns={columns} dataSource={companySalaries} size="small" bordered={false} locale={{emptyText: 'No hay registros'}}/>
                      }
                      <Row gutter={8} key={this.state.burstingKey}>
                        <Col xs={12} sm={12} md={6} lg={6} key={this.state.burstingKey}>
                          <FieldTitle title={"Ciclo de pagos"}/>
                          <FormItem >
                            {getFieldDecorator('companyRate',
                              {rules: [
                                {required: true, message: 'Por favor ingresa un tipo de pago'}
                              ]})(
                                <Select key={this.state.burstingKey} placeholder={"Tipo de salario"} showSearch={true} onSelect={(e) => this.changeSalariesValues(e, 'companyRate')} onChange={this.handleSalaryRate} allowClear={true} autoClearSearchValue={true}>
                                  <Select.Option value={"Quincenal"}>Quincenal</Select.Option>
                                  <Select.Option value={"Mensual"}>Mensual</Select.Option>
                                </Select>
                              )
                            }
                          </FormItem>  
                        </Col>
                        <Col xs={12} sm={12} md={6} lg={6}>
                          <FieldTitle title={"Fecha de salario 1"}/>
                          <FormItem >
                            {getFieldDecorator('companyFirstDate',
                              {rules: [
                                {required: true, message: 'Por favor ingresa una fecha de pago'}
                              ]})(
                                <Input key={this.state.burstingKey} className={"form-input-number"} placeholder={"Fecha de salario 1"} onChange={(e) => this.changeSalariesValues(e, 'companyFirstDate')}/>
                              )
                            }
                          </FormItem>  
                        </Col>
                        <Col xs={12} sm={12} md={6} lg={6}>
                          <FieldTitle title={"Fecha de salario 2"}/>
                          <FormItem >
                            {getFieldDecorator('companySecondDate',
                              {rules: [
                                {required: false, message: 'Por favor ingresa una fecha de pago'}
                              ]})(
                                <Input key={this.state.burstingKey} className={"form-input-number"} placeholder={"Fecha de salario 2"} onChange={(e) => this.changeSalariesValues(e, 'companySecondDate')}/>
                              )
                            }
                          </FormItem>  
                        </Col>
                        <Col xs={12} sm={12} md={6} lg={6}>
                          <FieldTitle title={"Fecha de reportes"}/>
                          <FormItem >
                            {getFieldDecorator('companyReportDate',
                              {rules: [
                                {required: true, message: 'Por favor ingresa una fecha de reporte'}
                              ]})(
                                <Input key={this.state.burstingKey} className={"form-input-number"} placeholder={"Fecha de reporte"} onChange={(e) => this.changeSalariesValues(e, 'companyReportDate')}/>
                              )
                            }
                          </FormItem>  
                        </Col>
                      </Row>
                      <Tooltip title="Agregar nuevo ciclo de pagos">
                        
                          <Icon type={"plus-circle"} className={"request-form-add"} onClick={() => this.addNewSalary()}/> 
                      </Tooltip>
                    </Row>
                  </Panel>
                  <Panel header="Informacin de la persona encargada" key="4">
                    <Row gutter={20} className={"form-request-rows"}>
                      <Col xs={12} sm={12} md={6} lg={6}>
                        <FieldTitle title={"Nombres"}/>
                        <FormItem >
                          {getFieldDecorator('memberName',
                            {rules: [
                              {required: true, message: 'Por favor ingrese el nombre del encargado'}
                            ]})(
                              <Input className={"form-input-number"} placeholder={"Nombres"}/>
                            )
                          }
                        </FormItem>  
                      </Col>
                      <Col xs={12} sm={12} md={6} lg={6}>
                        <FieldTitle title={"Apellidos"}/>
                        <FormItem >
                          {getFieldDecorator('memberLastName',
                            {rules: [
                              {required: true, message: 'Por favor ingrese el apellido del encargado'}
                            ]})(
                              <Input className={"form-input-number"} placeholder={"Apellidos"}/>
                            )
                          }
                        </FormItem>  
                      </Col>     
                      <Col xs={12} sm={12} md={8} lg={6} >
                        <FieldTitle title={"No. de identificacin"}/>
                        <FormItem>
                          {getFieldDecorator('memberId',
                            {rules: [
                              {required: true, message: 'Por favor ingresa un nmero de identificacin'}
                            ]})(
                              <Input className={"form-input-number"} placeholder={"No. de Identificacin"} />
                            )
                          }
                        </FormItem>
                      </Col>
                      <Col xs={12} sm={12} md={8} lg={6} >
                        <FieldTitle title={"Telfono celular"}/>
                        <FormItem>
                          {getFieldDecorator('phoneNumber',
                            {rules: [
                              {required: true, message: 'Por favor ingresa un telfono celular'}
                            ]})(
                              <Input className={"form-input-number"} placeholder={"Telfono celular"} />
                            )
                          }
                        </FormItem>
                      </Col>
                      <Col xs={12} sm={12} md={8} lg={6} >
                        <FieldTitle title={"Correo electrnico"}/>
                        <FormItem>
                          {getFieldDecorator('memberEmail',
                            {rules: [
                              {required: true, message: 'Por favor ingresa un correo electrnico'}
                            ]})(
                              <Input className={"form-input-number"} placeholder={"Correo electrnico"} />
                            )
                          }
                        </FormItem>
                      </Col>
                      <Col xs={12} sm={12} md={8} lg={6}>
                        <FieldTitle title={"Cargo"}/>
                        <FormItem >
                          {getFieldDecorator('profession',
                            {rules: [
                              {required: true, message: 'Por favor ingresa un cargo' }
                            ]})(
                              <Input className={"form-input-number"} placeholder={"Cargo"}/>
                            )
                          }
                        </FormItem>  
                      </Col>
                    </Row>
                  
                  </Panel>
                  <Panel header="Informacin del representante legal" key="3">
                    <Row gutter={20} className={"form-request-rows"} >

                      <Col xs={12} sm={12} md={8} lg={6} >
                        <FieldTitle title={"Nombres"}/>
                        <FormItem>
                          {getFieldDecorator('representantName',
                            {rules: [
                              {required: false, message: 'Por favor ingresa un nombre'}
                            ]})(
                              <Input className={"form-input-number"} placeholder={"Nombres"} />
                            )
                          }
                        </FormItem>
                      </Col>
                      <Col xs={12} sm={12} md={8} lg={6} >
                        <FieldTitle title={"Apellidos"}/>
                        <FormItem>
                          {getFieldDecorator('representantLastName',
                            {rules: [
                              {required: false, message: 'Por favor ingresa un apellido'}
                            ]})(
                              <Input className={"form-input-number"} placeholder={"Apellidos"} />
                            )
                          }
                        </FormItem>
                      </Col>
                      <Col xs={12} sm={12} md={8} lg={6} >
                        <FieldTitle title={"No. de identificacin"}/>
                        <FormItem>
                          {getFieldDecorator('representantId',
                            {rules: [
                              {required: false, message: 'Por favor ingresa un nmero de identificacin'}
                            ]})(
                              <Input className={"form-input-number"} placeholder={"No. de Identificacin"} />
                            )
                          }
                        </FormItem>
                      </Col>
                      <Col xs={12} sm={12} md={8} lg={6} >
                        <FieldTitle title={"Cargo"}/>
                        <FormItem>
                          {getFieldDecorator('representantProfession',
                            {rules: [
                              {required: false, message: 'Por favor ingresa un cargo para el representante'}
                            ]})(
                              <Input className={"form-input-number"} placeholder={"Cargo"} />
                            )
                          }
                        </FormItem>
                      </Col>
                      <Col xs={12} sm={12} md={8} lg={8} >
                        <FieldTitle title={"Telfono celular"}/>
                        <FormItem>
                          {getFieldDecorator('representantPhone',
                            {rules: [
                              {required: false, message: 'Por favor ingresa un telfono celular'}
                            ]})(
                              <Input className={"form-input-number"} placeholder={"Telfono celular"} />
                            )
                          }
                        </FormItem>
                      </Col>
                      <Col xs={12} sm={12} md={8} lg={8} >
                        <FieldTitle title={"Correo electrnico"}/>
                        <FormItem>
                          {getFieldDecorator('representantEmail',
                            {rules: [
                              {required: false, message: 'Por favor ingresa un correo electrnico'}
                            ]})(
                              <Input className={"form-input-number"} placeholder={"Correo electrnico"} />
                            )
                          }
                        </FormItem>
                      </Col>
                    </Row>
                  </Panel>  
                  
                </Collapse>
                <Col xs={24} sm={12} md={18} lg={19}/>
                <Col xs={24} sm={12} md={6} lg={5}>
                  <Button className={"request-confirm-button"} icon="plus-circle" 
                          onClick={() => this.createCompany()}>
                        Crear empresa
                  </Button> 
                </Col>
              </Form>
            </div>
        </Row>
        {
          this.props.companyResponse && 
            <Redirect to={routes.admin_company_management}/>
        }
      </div>
    );
  
  };
  
}

let CreateCompanyForm = Form.create()(CreateCompany);

const mapDispatchToProps = (dispatch) => {
  return {
    createCompany: (data) => dispatch(createCompany(data)),
  }
};


const mapStateToProps = (state) => {
  return {
    companyResponse: state.admin.companyResponse,
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(CreateCompanyForm);
